
*{
  margin: 0;
  padding: 0;
}

.nav{
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  /* height: 60px; */

}

#logo{
  width: 20%;
  height: 150px;
  margin: 10px 0px;
  border-radius: 50%;
  display: block;
  margin: auto;
}

.nav ul{
  display: flex;
  list-style: none;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.nav ul li{
  margin: 21px 4em 0px;
  color: white;
  font-size:large;
  cursor: pointer;

}
.myNav{
  position: fixed;
  top: 0;
  width: 100%;
  background-color: black;
}

.background{
  background: rgba(233, 233, 233, 0.7) url("./bg/bg4.jpg") no-repeat center center fixed;
  background-size: cover;
  background-blend-mode: darken;
}

.about h1{
  text-align: center;
}
.about{
  text-align: justify;
  padding: 140px;
}


.sec1{
  height: 100vh;
}

.sec2{
  /* border: 2px solid green; */
  width: 80%;
  margin: 40px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card{
  margin: 5px;
}

.card:hover{
  transform: scale(1.2);
  z-index: 100;
  box-shadow: 0px 0px 12px black;
}

.bg-black{
  border: 2px solid green; 
  height: 60vh;
  background-color: black;
}

/* .bg-black img{
  width: 800px;
  height: 40vh;
} */


.h-60{
  height: 80vh;
}

.contact-form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* .center{

  width: 60vw;

} */

.myInput{
  display: block;
  background: none;
  border: 2px solid white;
  width: 100%;
  margin: 4px;
  padding: 4px;
  border-radius: 10px;
  color: white;
  
}

