.footer-split{
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}

.card-img-top{
  height: 200px;
}

.popUp{
  width: 20vw;
  font-size:x-large;
  margin: auto;
  position: fixed;
  top: 20%;
  left: 40%;
}

